body {
  position: relative;
}

.conteiner {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.timer {
  margin-top: 50px;
  font-size: 24px;
  display: flex;
}

.field {
  color: red;
  flex-direction: column;
  align-items: center;
  margin-left: 30px;
  font-weight: 600;
  display: flex;
}

.field:first-child {
  margin-left: 0;
}

.flatpickr-input {
  height: 30px;
  border-radius: 5px;
  outline: none;
  margin-right: 15px;
  padding: 15px;
}

button {
  width: 60px;
  height: 35px;
  color: #f0f8ff;
  cursor: pointer;
  background-color: gray;
  border: none;
  border-radius: 15px;
  font-size: 16px;
}

.succsses-button {
  background-color: green;
}

.succsses-button:hover {
  box-shadow: 0 4px 4px rgba(2, 236, 33, .745);
}

.stop-button {
  background-color: red;
}

.stop-button:hover {
  box-shadow: 0 4px 4px rgba(236, 2, 2, .745);
}

